<template>
    <div class="about">
        <HEADER logo="true" color="white" @showMenu="show"></HEADER>
        <div class="hidemenu">
            <hidemenu @closeMenu="close"></hidemenu>
        </div>
        <about></about>
         <div v-if="isloading">
              <loadingpage></loadingpage>
         </div>
       
    </div>
</template>

<script>
import HEADER from '../components/Header'
import hidemenu from '../components/HideMenu'
import gsap from "gsap";
import loadingpage from '../components/Loadingpage'
import about from '../components/about'
export default {
     data() {
        return {
            isloading: true
        }
    },
    mounted() {
        setTimeout(this.closeloading, 4000);
    },
    methods: {
        show() {
            gsap.to('.hidemenu', {
                left: 0,
                opacity: 1,
                duration: 0.5
            })
        },
        close() {
            gsap.to('.hidemenu', {
                left: '100%',
                opacity: 0,
                duration: 0.4
            })
        },
        closeloading() {
            this.isloading = false
        }
    },
    components: {
        HEADER,
        hidemenu,
        loadingpage,
        about
    }
}
</script>

<style lang="scss" scoped>
.about {
    height: 100vh;
    position: relative;
    overflow: hidden;
    background-color:#b8b8b8;
    // background-color: black;
}
.hidemenu {
    position: absolute; // top: -100%;
    top: 0;
    left: 100%;
    z-index: 1;

}


</style>